<template>
  <div v-if="product" class="product-detail">
    <h1>{{ product.title }}</h1>
    <img :src="product.image" alt="Product Image" />
    <p>Цена: {{ product.price }} $</p>
    <p>Категория: {{ product.category }}</p>
    <p>Описание: {{ product.description }}</p>
    <p>Оценка: {{ product.rating.rate }} / 5</p>
    <p>Количество оценок: {{ product.rating.count }}</p>
    <button @click="addToCart">Добавить в корзину</button>
  </div>
  <div v-else>
    <p class="loading-message">Загрузка продукта...</p>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      product: null,
    };
  },
  methods: {
    async fetchProduct() {
      const id = this.$route.params.id;
      try {
        const response = await axios.get(`https://fakestoreapi.com/products/${id}`);
        this.product = response.data;
      } catch (error) {
        console.error("Ошибка загрузки продукта:", error);
      }
    },
    addToCart() {
      const cart = JSON.parse(localStorage.getItem("cart")) || [];
      cart.push(this.product);
      localStorage.setItem("cart", JSON.stringify(cart));
      alert("Товар добавлен в корзину!");
    },
  },
  mounted() {
    this.fetchProduct();
  },
};
</script>

<style>
.product-detail {
  max-width: 600px; 
  margin: 20px auto; 
  padding: 20px; 
  background-color: #f9f9f9; 
  border: 1px solid #ddd; 
  border-radius: 10px; 
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
}

.product-detail h1 {
  font-size: 1.8em; 
  color: #333; 
  margin-bottom: 20px; 
}

.product-detail img {
  max-width: 100%; 
  border-radius: 10px; 
  margin-bottom: 20px; 
}

.product-detail p {
  font-size: 1em; 
  color: #666; 
  margin: 10px 0; 
}

.product-detail button {
  background-color: #28a745; 
  color: white; 
  border: none; 
  border-radius: 5px; 
  padding: 10px 15px; 
  cursor: pointer; 
  transition: background-color 0.3s; 
  margin-top: 20px; 
  width: 100%; 
}

.product-detail button:hover {
  background-color: #218838; 
}

.loading-message {
  text-align: center; 
  font-size: 1.2em; 
  color: #666;
  margin-top: 20px; 
}

@media (max-width: 600px) {
  .product-detail {
    padding: 15px;
    margin: 10px;
  }

  .product-detail h1 {
    font-size: 1.5em;
  }

  .product-detail p {
    font-size: 0.9em;
  }

  .product-detail button {
    padding: 12px;
  }
}
</style>