<template>
  <div class="product-card" @click="handleClick">
    <img :src="product.image" alt="Product Image" />
    <h3>{{ product.title }}</h3>
    <p>Цена: {{ product.price }} $</p>
    <p>Категория: {{ product.category }}</p>
    <p>Оценка: {{ product.rating.rate }} / 5</p>
    <button @click.stop="addToCart">В корзину</button>
  </div>
</template>

<script>
export default {
  props: ["product"],
  methods: {
    handleClick() {
      this.$emit("click");
    },
    addToCart() {
      const cart = JSON.parse(localStorage.getItem("cart")) || [];
      cart.push(this.product);
      localStorage.setItem("cart", JSON.stringify(cart));
      alert("Товар добавлен в корзину!");
    },
  },
};
</script>

<style>
.product-card {
  background-color: #f9f9f9; 
  border: 1px solid #ddd; 
  border-radius: 10px; 
  padding: 16px; 
  margin: 8px; 
  cursor: pointer; 
  transition: box-shadow 0.3s, transform 0.2s; 
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
  max-width: 300px; 
  width: 100%; 
}

.product-card:hover {
  transform: scale(1.03); 
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); 
}

.product-card img {
  max-width: 100%; 
  border-radius: 10px; 
}

.product-card h3 {
  font-size: 1.2em; 
  color: #333; 
  margin: 10px 0; 
}

.product-card p {
  font-size: 0.9em; 
  color: #666;
  margin: 5px 0; 
}

.product-card button {
  background-color: #18b7d3; 
  color: white; 
  border: none; 
  border-radius: 5px; 
  padding: 10px 15px; 
  cursor: pointer; 
  transition: background-color 0.3s; 
  margin-top: 10px; 
  width: 100%; 
}

.product-card button:hover {
  background-color: #18b7d3; 
}

@media (max-width: 600px) {
  .product-card {
    padding: 12px; 
  }
  
  .product-card h3 {
    font-size: 1.1em; 
  }

  .product-card p {
    font-size: 0.85em; 
  }

  .product-card button {
    padding: 8px; 
  }
}
</style>