<template>
  <div>
    <h1>Список товаров</h1>

    <select v-model="selectedCategory" @change="filterByCategory">
      <option value="">Все категории</option>
      <option v-for="category in categories" :key="category" :value="category">
        {{ category }}
      </option>
    </select>

    <div class="product-list">
      <ProductCardComponent
        v-for="product in filteredProducts"
        :key="product.id"
        :product="product"
        @click="viewProduct(product.id)"
      />
    </div>

    <PaginationComponent :total="totalProducts" :currentPage="page" @pageChange="fetchProducts" />
  </div>
</template>

<script>
import axios from "axios";
import ProductCardComponent from "./ProductCardComponent.vue";
import PaginationComponent from "./PaginationComponent.vue";

export default {
  components: { ProductCardComponent, PaginationComponent },
  data() {
    return {
      products: [],
      categories: [],
      filteredProducts: [],
      selectedCategory: "",
      page: 1,
      totalProducts: 0,
    };
  },
  methods: {
    async fetchProducts(page = 1) {
      this.page = page;
      const response = await axios.get(`https://fakestoreapi.com/products?limit=10&page=${page}`);
      this.products = response.data;
      this.filteredProducts = response.data;
      this.totalProducts = response.headers["x-total-count"];
    },
    async fetchCategories() {
      const response = await axios.get("https://fakestoreapi.com/products/categories");
      this.categories = response.data;
    },
    filterByCategory() {
      if (this.selectedCategory === "") {
        this.filteredProducts = this.products;
      } else {
        this.filteredProducts = this.products.filter(
          (product) => product.category === this.selectedCategory
        );
      }
    },
    viewProduct(id) {
      this.$router.push(`/product/${id}`);
    },
  },
  mounted() {
    this.fetchProducts();
    this.fetchCategories();
  },
};
</script>

<style>
.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;  
  margin: 20px 0; 
}
</style>